import { Table, TableBody } from '@material-ui/core';
import React from 'react';

import TranslationTableHead from './TranslationTableHead';
import TranslationTableRow, { TranslationRowData } from './TranslationTableRow';

interface Props {
  rows: Array<TranslationRowData>;
}

export default function TranslationTable(props: Props) {
  return (
      <Table aria-label="Translations">
        <TranslationTableHead />
        <TableBody>
          {props.rows.map(row => <TranslationTableRow key={row.translation.link} row={row} />)}
        </TableBody>
      </Table>
  );
}
