import parse from 'csv-parse/lib/sync';
import escapeRegExp from 'lodash.escaperegexp';

import { TranslationRowData } from '../components/TranslationTableRow';

interface AndroidRow {
  englishText: string,
  englishFile: string,
  englishLine: string,
  translationText: string,
  translationFile: string
  translationLine: string,
  projectDirectory: string,
  resourceDirectory: string
}

const columns = {
  'android': [
    'englishText',
    'englishFile',
    'englishLine',
    'translationText',
    'translationFile',
    'translationLine',
    'projectDirectory',
    'resourceDirectory',
  ]
}

function getTextMatches(text: string, regExp: RegExp): [number, number][] {
  return Array.from(text.matchAll(regExp)).map(match => [match.index!!, match.index!! + match[0].length])
}

export function getRowsFromCsv(
    csv: string,
    query: string,
    useRegularExpression: boolean,
    superproject: string
): Array<TranslationRowData> {
  const queryRegExp = new RegExp(useRegularExpression ? query : escapeRegExp(query), 'giu');
  switch (superproject) {
    case 'android': {
      const rows = parse(csv, { columns: columns[superproject] }) as Array<AndroidRow>;
      let count = 0;
      return rows.flatMap(row => {
          if (count >= 1000) {
            return [];
          }
          const englishMatches = getTextMatches(row.englishText, queryRegExp);
          const translationMatches = getTextMatches(row.translationText, queryRegExp);
          if (!(englishMatches.length || translationMatches.length)) {
            return [];
          }
          ++count;
          return [{
            english: {
              text: row.englishText,
              link: `https://cs.android.com/android/platform/superproject/+/master:${row.projectDirectory}/${
                  row.resourceDirectory}/${row.englishFile};l=${row.englishLine}`,
              matches: englishMatches,
            },
            translation: {
              text: row.translationText,
              link: `https://cs.android.com/android/platform/superproject/+/master:${row.projectDirectory}/${
                  row.resourceDirectory}/${row.translationFile};l=${row.translationLine}`,
              matches: translationMatches,
            },
            project: {
              text: row.projectDirectory,
              link: `https://cs.android.com/android/platform/superproject/+/master:${row.projectDirectory}/`,
            },
          }];
        });
    }
    default:
      return [];
  }
}
